import { connect } from 'react-redux'
import { addAddress, editAddress } from '../actions/addresses'
import Addresses, {
	IAddressEditorActionProps,
} from '../components/AddressEditor'

const mapStateToProps = () => ({})
const mapDispatchToProps: IAddressEditorActionProps = {
	addAddress,
	editAddress,
}

export default connect(
	mapStateToProps,
	mapDispatchToProps,
)(Addresses)



