import createActionCreator, {
	IStandardAction,
} from '../utils/createActionCreator'
import { Dispatch } from 'redux'
import api from '../utils/api'
import { IDate } from '../models/date'

export interface IDeliveryWindow {
	duration: string
	query: string
	date: IDate
}

export const SET_DELIVERY_WINDOW = 'SET_DELIVERY_WINDOW'
export type SetDeliveryWindowAction = IStandardAction<IDeliveryWindow>
const setDeliveryWindow = createActionCreator(
	SET_DELIVERY_WINDOW,
	(deliveryWindow: IDeliveryWindow) => deliveryWindow,
)
export const requestDeliveryWindow = () => (
	dispatch: Dispatch<any>,
) => {
	api.requestNextDeliveryWindow()
		.then((data) => {
			dispatch(setDeliveryWindow(data))
		})
}



