import React from 'react'
import { IBasketItems } from '../../actions/basket'
import styles from './styles.sass'
import BottomBar from '../BottomBar'
import Bottle from '../Bottle'
import cn from 'classnames'
import isProductSmall from '../../utils/isProductSmall'

export interface IBasketPreviewOwnProps {
	isActive: boolean
}
export interface IBasketPreviewProps {
	items: IBasketItems
}

export interface IBasketPreviewActionProps {
	onOpenDetail: () => void
}

export default class BasketPreview extends React.Component<
	IBasketPreviewProps & IBasketPreviewActionProps & IBasketPreviewOwnProps
> {
	render() {
		const { items, onOpenDetail, isActive } = this.props

		return (
			<BottomBar isActive={isActive}>
				<div className={styles.items}>
					{Object.values(items).map((item) => (
						<div
							key={item.product.id}
							className={cn(styles.item, isProductSmall(item.product) && styles.viewSmall)}
							style={{ color: item.product.color }}
						>
							<div className={styles.quantity}>{item.quantity}</div>
							<div className={styles.image}>
								<Bottle className={styles.imageIn} />
							</div>
						</div>
					))}
				</div>
				<div className={styles.continue}>
					Proceed
					<i className="fas fa-arrow-right"></i>
				</div>
				<button
					className={styles.action}
					role="button"
					aria-label="Show basket"
					onClick={onOpenDetail}
				/>
			</BottomBar>
		)
	}
}



