import handleActions from '../utils/handleActions'
import {
	SET_ADDRESSES,
	SetAddressesAction,
	ADD_ADDRESS,
	AddAddressAction,
	REMOVE_ADDRESS,
	RemoveAddressAction,
	EDIT_ADDRESS,
	EditAddressAction,
} from '../actions/addresses'

export interface IAddressesState {
	[id: string]: IAddress
}

export interface IAddress {
	id: string
	title: string
	address: string
}

const initialState: IAddressesState = {}

// tslint:disable:function-name
export default handleActions(
	{
		[SET_ADDRESSES](_, { payload }: SetAddressesAction) {
			return {
				...payload,
			}
		},
		[ADD_ADDRESS](state, { payload }: AddAddressAction) {
			return {
				...state,
				[payload.id]: payload,
			}
		},
		[REMOVE_ADDRESS](state, { payload }: RemoveAddressAction) {
			const newState = { ...state }
			delete newState[payload.id]
			return newState
		},
		[EDIT_ADDRESS](state, { payload }: EditAddressAction) {
			const newState = { ...state }
			delete newState[payload.oldAddress.id]
			newState[payload.newAddress.id] = payload.newAddress
			return newState
		},
	},
	initialState,
)



