import handleActions from '../utils/handleActions'
import {
	SET_DELIVERY_WINDOW,
	SetDeliveryWindowAction,
	IDeliveryWindow,
} from '../actions/orders'

export interface IOrdersState {
	deliveryWindow: null | IDeliveryWindow
}

const initialState: IOrdersState = {
	deliveryWindow: null,
}

// tslint:disable:function-name
export default handleActions(
	{
		[SET_DELIVERY_WINDOW](state, { payload }: SetDeliveryWindowAction) {
			return {
				...state,
				deliveryWindow: payload,
			}
		},
	},
	initialState,
)



